import './App.css';
import { AppBar, Paper, InputBase, Container, CardMedia, CardActionArea, Grid, Box, CircularProgress } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import { useEffect, useState } from 'react';
import axios from 'axios';

function App() {
  const [loading, setLoading] = useState(false)
  const [musics, setMusics] = useState([])
  const [play, setPlay] = useState("")
  const [name, setName] = useState("")
  const [title, setTitle] = useState("")

  useEffect  (() => {
    loadSearch();
  },[]);

  const loadSearch = (evt) => {
    setLoading(true);
    if (evt === undefined || evt === '') {
      axios.get('https://api.napster.com/v2.2/tracks/top?apikey=N2EwY2E0Y2ItMTI4ZS00ZTg3LTg4MmEtY2JiMTdhODQxYzUw&range=day')
      .then((response) => {
        setMusics(response.data.tracks);
        setLoading(false);
        console.log(response);
      })
      .catch((error) => {
        setMusics([]);
        setLoading(false);
        console.log(error);
      });
    } else {
      axios.get(`https://api.napster.com/v2.2/search/verbose?apikey=N2EwY2E0Y2ItMTI4ZS00ZTg3LTg4MmEtY2JiMTdhODQxYzUw&query=${evt}&type=track`)
      .then((response) => {
        setMusics(response.data.search.data.tracks);
        setLoading(false);
        console.log(response);
      })
      .catch((error) => {
        setMusics([]);
        setLoading(false);
        console.log(error);
      });
    }
  }

  useEffect  (() => {
    if (play !== '') {
      play.play();
    }
  },[play]);

  return (
    <Container maxWidth="sm" sx={{ p: '0 20px', backgroundColor: '#000000', minHeight: '100vh' }}>
      <AppBar position="sticky" sx={{ p: '10px 0', backgroundColor: '#000000' }}>
        <Paper sx={{ p: '2px 11px', display: 'flex', alignItems: 'center', backgroundColor: '#161616', borderRadius: '5px' }}>
          <SearchIcon sx={{color: "#ffffff"}} />
          <InputBase id="input" sx={{ ml: 2, color: '#ffffff' }} placeholder="Cari" fullWidth="true" 
          onChange={(e) => loadSearch(e.target.value)} autoFocus autoComplete="off" />
        </Paper>
        {name === "" && title === "" ? null : <h6 id="onplaying">On Playing : {name} - {title}</h6>}
      </AppBar>
      {loading ? (
        <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '75vh' }}><CircularProgress /></Box>
      ) : (
        musics.map((item) => 
          <CardActionArea sx={{ p: '5px 0' }}>
            <Paper sx={{ backgroundColor: '#161616', color: '#ffffff', borderRadius: '5px' }} 
            onClick={() => {if (play !== '') {play.pause()}; setPlay(new Audio(item.previewURL)); setName(item.artistName); setTitle(item.name)}}>
              <Grid container sx={{ alignItems: 'center' }}>
                <Grid item xs={8} container direction="column">
                  <Grid item sx={{ width: '100%' }}>
                    <h6 id="title">{item.name}</h6>
                  </Grid>
                  <Grid item sx={{ width: '100%' }}>
                    <h6 id="subtitle">{item.artistName}</h6>
                  </Grid>
                </Grid>
                <Grid item xs={4}>
                  <CardMedia sx={{ borderRadius: '0 5px 5px 0' }}
                  component="img" height="67px"
                  image={`https://api.napster.com/imageserver/v2/albums/${item.albumId}/images/633x422.jpg`}
                  alt="No image" />
                </Grid>
              </Grid>
            </Paper>
          </CardActionArea>)
      )}
    </Container>
  );
}

export default App;
